.list {
  list-style: none;
}

.listItem {
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  padding: 20px;
  width: 1080px;
}

.listItem:hover,
.listItem:focus {
  background-color: rgb(255, 178, 35);
  color: #e9e9e9;
}

.listWrap {
  width: 1200px;
  max-height: 400px;
  overflow: overlay;
  margin: 50px auto;
  border: 1px solid #e9e9e9;
  border-radius: 10px;
  box-shadow: rgb(0 0 0 / 50%) 0px 0px 10px;
}

.listName {
  margin: 0 0 15px;
  font-size: 22px;
  font-weight: 700;
}

.error {
  font-size: 30px;
  color: #272727;
  margin-left: 100px;
  margin-top: 40px;
}

.loading {
  display: flex;
  width: 100%;
  position: relative;
  transition: 0.5s;
  top: 0;
  left: 0;
}